.CreateTodoButton {
    z-index: 1;
    background-color: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    /* bajamos un poco la posicion del boton  */
    top: calc(100% - 190px);
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    transform: rotate(0);
    transition: .3s ease;
  }
  
  .CreateTodoButton:hover {
      z-index: 1;
      transform: rotate(224deg);
      background-color: rgb(50, 255, 50);
  }